import React, { useRef, useEffect } from "react";
import "./LandingPage.css";
import Header from "../../components/LandingPage/Header/Header.js";
import Ambassadors from "../../components/LandingPage/Ambassadors/Ambassadors.js";
import Footer from "../../components/LandingPage/Footer/Footer.js";
import { ambasadors } from "../../data/ambasadors.js";
import Contact from "../../components/LandingPage/Contact/Contact.js";
import Quiz from "../../components/LandingPage/Quiz/Quiz.js";
import Welcome from "../../components/LandingPage/Welcome/Welcome.js";
import TextWithImage from "../../components/TextWithImage/TextWithImage.js";
import Mockup from "../../assets/mockup.png";
import Stanowisko from "../../assets/DSC_0084.webp";
import Sklep from "../../assets/shop.webp";
import Site from "../../assets/site.webp";

function LandingPage() {
  const contactRef = useRef(null);

  useEffect(() => {
    const scrollToTop = () => {
      window.scrollTo(0, 0);
    };

    window.requestAnimationFrame(scrollToTop);

    const timeoutId = setTimeout(scrollToTop, 0);

    return () => clearTimeout(timeoutId);
  }, []);

  return (
    <div className="landingpage__container">
      <Header contactRef={contactRef} />
      <Welcome />
      <TextWithImage
        text="Strony internetowe stanowi niezbdny element wspczesnego wiata biznesu. Dziki nim firmy mog skutecznie komunikowa si z klientami, prezentowa swoje produkty i usugi, budowa mark oraz zwiksza swoj widoczno w internecie."
        image={Site}
      />
      <TextWithImage
        text="WEBTECH SOLUTIONS specjalizuje si w tworzeniu nowoczesnych i funkcjonalnych sklepw internetowych, ktre pomagaj naszym klientom osiga sukcesy w handlu online."
        image={Sklep}
        reverse
      />
      <TextWithImage
        text="Korzystamy z najnowszych technologii i profesjonalnego sprztu, aby zapewni najwysz jako usug. Nasz zesp dowiadczonych specjalistw dba o kady detal, od projektowania po wdroenie, gwarantujc, e Twoja platforma e-commerce bdzie dziaa sprawnie i niezawodnie."
        image={Stanowisko}
      />
      <Ambassadors ambasador={ambasadors} />
      <Quiz />
      <div className="contact__container">
        <Contact ref={contactRef} />
      </div>
      <Footer />
    </div>
  );
}

export default LandingPage;
